.home_page .sys_detail {
  width: 100%;
  height: 130px;
  padding-left: 30px;
  border: 1px solid #6666;
}
.home_page .soul_menu {
  margin-top: 20px;
}
.home_page .soul_menu_ul {
  width: 100%;
  display: flex;
  padding: 0;
  flex-wrap: wrap;
}
.home_page .soul_menu_ul li {
  flex: 0 1 25%;
  margin-right: 10px;
  margin-bottom: 10px;
}
.home_page .soul_menu_ul .soul_card > .ant-card-body div {
  display: inline-block;
  cursor: pointer;
}
.home_page .soul_menu_ul .soul_card > .ant-card-body div span {
  vertical-align: middle;
  margin-right: 10px;
}
.home_page .soul_menu_ul .soul_card > .ant-card-body div span:nth-child(1) {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #e6f7ff;
  text-align: center;
  line-height: 30px;
}
.home_page .soul_menu_ul .soul_card > .ant-card-body div span:nth-child(1) i {
  color: #6236FF;
}
.home_page .soul_menu_ul .soul_card > .ant-card-body div:nth-child(2) {
  float: right;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #6236FF;
  text-align: center;
  line-height: 23px;
  color: #6236FF;
  margin-top: 3px;
}
@media screen and (min-width: 1300px) {
  .home_page .soul_menu_ul li {
    flex: 0 1 16%;
  }
}
@media screen and (max-width: 900px) and (min-width: 800px) {
  .home_page .soul_menu_ul li {
    flex: 0 1 20%;
  }
}
@media screen and (max-width: 768px) {
  .home_page .soul_menu_ul li {
    flex: 0 1 33%;
  }
}
.home_index {
  position: absolute;
  background: #f5f5f5;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
}
.chart_content {
  margin: 0 20px;
  overflow: hidden;
  padding: 10px;
}
.chart_content > div {
  box-shadow: 0px 0px 10px 0px #c4c4c4;
  border-radius: 7px;
  background: #fff;
  padding: 13px;
  display: inline-block;
}
.chart_content > div > h3 {
  font-size: 13px;
  font-family: MicrosoftYaHei;
  color: #161616;
  line-height: 17px;
  display: flex;
  align-items: center;
}
.chart_content > div > h3 > span {
  display: inline-block;
  width: 3px;
  height: 16px;
  background: #6236ff;
  margin-right: 9px;
}
.home_page_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}
.home_page_title .title_tag {
  width: 24%;
  padding: 10px 13px 13px 13px;
  margin-bottom: 20px;
  background: #ffffff;
  box-shadow: 0px 0px 15px 0px #c4c4c4;
  border-radius: 10px;
  display: flex;
}
.home_page_title .title_tag .title_tag_left {
  font-size: 60px;
  color: #161616;
  width: 40%;
  border-right: 1px solid #E6E6E6;
}
.home_page_title .title_tag .title_tag_right {
  flex: 1 1;
}
.home_page_title .title_tag .title_tag_right h3 {
  font-size: 11px;
  font-family: MicrosoftYaHei;
  color: #9d9d9d;
  text-align: right;
}
.home_page_title .title_tag .title_tag_right > div {
  text-align: right;
  padding-top: 21px;
}
.home_page_title .title_tag .title_tag_right > div img {
  width: 41px;
  height: 37px;
}
.home_page_title .title_tag p.work_odd {
  font-size: 28px;
  margin: 0;
  color: #333;
  font-weight: 600;
  display: flex;
  align-items: flex-end;
}
.home_page_title .title_tag p.work_odd .work_ratio {
  font-weight: normal;
  font-size: 18px;
}
@media screen and (max-width: 1280px) {
  h3 {
    margin-bottom: 10px !important;
  }
  p.work_odd {
    font-size: 24px !important;
  }
  p.work_odd .work_ratio {
    font-size: 14px !important;
  }
}
/* 菜单项 */
.add_menu_soul .add_menu_dad {
  display: inline-block;
  vertical-align: middle;
}
.add_menu_soul .add_menu_son {
  margin: 5px 0px;
  display: inline-block;
  border-left: 1px solid #e9e9e9;
  padding-left: 10px;
  max-width: 600px;
  vertical-align: middle;
}
